<template>
  <view class="find">
    <findCell title="面试技巧"></findCell>
    <view v-for="(item, index) of infoList" :key="index" class="shareItem">
      <shareItem :info="item"></shareItem>
    </view>
    <findCell title="市场数据"></findCell>
    <view class="marketData">
      <marketData :chart="marketData"></marketData>
    </view>
    <findCell title="面经分享"></findCell>
    <view class="shareInterview"></view>
  </view>
</template>
<script>
import findCell from './findCell'
import shareItem from './share-item'
import marketData from './marketData'
import { articlesTechnic, chartDataHot } from '@/api/find'
import '@/static/iconfont/iconfont.css'
export default {
  components: { findCell, shareItem, marketData },
  data () {
    return {
      infoList: [],
      marketData: {},
      qData: {
        start: 0,
        limit: 3,
        q: ''
      }
    }
  },
  onPullDownRefresh () {
    console.log('refresh')
    setTimeout(function () {
      uni.stopPullDownRefresh()
    }, 1000)
  },
  async mounted () {
    //面试技巧
    const res = await articlesTechnic(this.qData)
    this.infoList = res.list
    // 市场数据
    const res1 = await chartDataHot()
    res1.yearSalary.reverse()
    this.marketData = res1
  }
}
</script>

<style lang="scss" scoped>
.find {
  padding: 0 14.5px;
  .shareItem {
    padding: 14.5px 0;
  }
}
</style>
